* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
::-webkit-scrollbar{
    width: 10px;
    background-color: rgb(163, 163, 163);
}
::-webkit-scrollbar-thumb{
    background-color: var(--primarycolor);
    border-radius: 12px;
}

:root {
  --primarycolor: hsl(225, 70%, 37%);
  --SecondaryColor: hsl(188, 17%, 95%);
  --colorOne: hsl(205, 74%, 70%);
  --colorTwo: hsl(287, 67%, 54%);
  --HoverColor: hsl(225, 56%, 59%);
  --whiteColor: hsl(0, 8%, 100%);
  --blackColor: hsl(0, 0%, 12%);
  --textColor: hsl(240, 1%, 48%);
  --borderColor: hsl(8, 0%, 83%);
  --whiteColorDeam: hs1(8, 9%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(145, 145, 145);
  --inputColor: hsl(330, 12%, 97%);
  --boxShadow: box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px,rgba(0,0,0,0.05) 0px 4px 6px -2px ;
  --transition: .4s ease-in-out ;
}

/* =========== fefault styling ========= */

body{
    background-color: var(--whiteColor);
}
a{
    text-decoration: none;
    cursor: pointer;
}
li{
    list-style: none;
    cursor: pointer;
}
.section{
    padding: 4rem 0;
}
.container{
    padding:0 1.5rem;
}
.icon{
    font-size: 1.5rem;
    align-items: center;
}
.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
}
.none{
    display: none;
}
.btn{
    padding: .7rem 2.5rem;
    background-color: var(--primarycolor);
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
    font-weight: bold;
    color: var(--whiteColor);

    &:hover{
        background-color: rgb(28, 28, 85);
        transition: .5s;

    }
}
.btn a{
    color: var(--whiteColor);
    font-family: 500;
    cursor: pointer;
}
video, img{
    font: 100%;
    color: var(--greyText);
    cursor: pointer;
}
p{
    font-size: 14px;
    color: var(--greyText);
    line-height: 22px;
}
h4{
    padding: 1rem 0;
    font-weight: 700;
    color: var(--blackColor);
}
h2{
    font-size: 25px;
    padding: 1rem 0;
    color: var(--blackColor);
}

/* ============= navbar styling ======= */
.navBar{
    position: fixed;
    flex-direction: column;
    width: 100%;
    z-index: 1000;
    .navBarOne , .navBarTwo{
        justify-content: space-between;
        padding: 1rem;
        width: 100%;
    }
    .navBarOne{
        background-color: var(--SecondaryColor);
        padding: 1rem 5rem;
        .icon:hover{
            color: var(--primarycolor);
            cursor: pointer;
        }
        .atb{
            gap: 1rem;

        }
        .atb span{
            color: var(--blackColor);
            font-size: 15px;
            cursor: pointer;
            &:Hover{
                color: var(--primarycolor);
                font-size: 700;
            }
        }
    }

    .navBarTwo{
        display: flex;
        position: relative;
        align-items: center;
        justify-content: space-between;
        top: 0rem;
        padding: 1.5rem 1rem;
        transition: .3s ease-in-out;

        .logoDiv{
            .logo{
                width: 30%;
                margin-top: -10px;
            }
            .logo img{
                width: 500px;
            }
        }

    }
    .navbar_with_bg{
        background-color: var(--SecondaryColor);
        padding: .5rem 1rem;
        box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px,rgba(0,0,0,0.05) 0px 4px 6px -2px ;
    }
}

@media screen and (max-width: 768px) {
    .navBarMenu{
        position: absolute;
        background-color: var(--SecondaryColor);
        border-radius: 10px;
        padding: 1.5rem;
        right: -50%;
        width: 80%;
        top: 5rem;
        transform: translateX(50%);
        z-index: 2000;
        box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px,rgba(0,0,0,0.05) 0px 4px 6px -2px; ;
        border: 3px solid var(--whiteColor);
        transition: .4s ease-in-out;
        &
        .menu{
            flex-direction: column;
            gap: .5rem;
            text-align: center;

            .listItem{
                padding: .5rem 0;
                cursor: pointer;

                &:hover{
                    color: var(--primarycolor);
                    font-weight: 700;
                }
            }
        }
        .btnOne{
            display: block;
            justify-content: center;
            margin: auto;
            margin-top: 40px;
        }
    }
    .btnTwo{
        display: none;
    }
    .toggleIcon{
        .icon{
            font-size: 30px;
            color: var(--blackColor);
            
        }
    }
    .showNavBar{
        right: 50%;
        transition: .5s ease-in-out;
    }
}

/* ============= Home styling ======= */
.home{
    padding-top:10rem ;
    text-align: center;
    gap: 2rem;
    margin: auto;
    flex-direction: column;
    

    .mainText{
        color: var(--blackColor);
        font-weight: 700;
        font-size: 15px;
        line-height: 2.5rem;
        text-align: center;

    }
    .homeImages{
        width: 90%;
        margin: 1rem 0;
        justify-content: center;
        position: relative;
        border-radius: 5rem;
        align-items: center;
        flex-direction: column;

        .videoDiv{
            width: 100%;

            .video{
                border-radius: 10rem;
                width: 100%;
            }
        }
        .plane{
            position: absolute;
            width: 100%;
            top: -25px;
            margin-left: 10px;
            
        }
    }
}

/* ============= search styling ======= */
.search{
    .sectionContainer{
        width: 100%;
        padding: 1.5rem;
        border: 3px solid var(--greyBg);
        border-radius: 16px;
        row-gap: 2rem;

        .btns{
            background-color: var(--SecondaryColor);
            padding: .5rem;
            border-radius: 1rem;
            flex-direction: column;
            margin: auto;
            width: 100%;

            .singleBtn{
                padding: .8rem 1.5rem;
                font-size: 13px;
                border-radius: 3rem;
                width: 100%;
                text-align: center;
                font-weight: bold;

                &:hover{
                    background-color: var(--primarycolor);
                    color: var(--whiteColor);
                    transition: .4s ease-in-out;
                }
            }
            .active{
                background-color: var(--primarycolor);
                color: var(--whiteColor);
                font-weight: 500;
            }
        }
        .searchInputs{
            width: 100%;
            justify-content: center;
            flex-direction: column;
            gap: 1.5rem;

            .singleInput{
                .iconDiv{
                    padding: 16px 13px;
                    border-radius: 3rem;
                    background-color: var(--SecondaryColor);
                    margin-right: 1rem;

                    .icon{
                        color: var(--blackColor);
                        opacity: .8;
                    }
                }
                .texts h4{
                    padding-top:0 ;
                    padding-bottom: .7rem;
                    color: var(--blackColor);
                    font-weight: 700;
                }
                .texts input{
                    width: 95%;
                    border: none;
                    outline: none;
                    background-color: transparent;

                    &::placeholder{
                        font-size: 14px;
                        opacity: .7;
                    }
                }
            }

        }
    }
}


/* ============= support styling ======= */

.support{
    .sectionContainer{
        width: 90%;
        margin: auto;
        .titlesDiv{
        text-align: center;
        }
        .titlesDiv small{
            letter-spacing: .8rem;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 14px;
            color: var(--blackColor);
            line-height: 1rem;
        }
    }
    .infoDiv{
        margin-top: 1.5rem;
        gap: 1.5rem;
        .textDiv{
            gap: 1.5rem;

            .singleInfo{
                .number{
                    background-color: var(--primarycolor);
                    padding: 5px 13px ;
                    border-radius: 13px;
                    text-align: center;
                    font-size:14px ;
                    color: var(--whiteColor);

                }
                .colorOne{
                    background-color: var(--colorOne);
                }
                .colorTwo{
                    background-color: var(--colorTwo);
                }
            }
        }
        .imgDiv{
            margin: auto;

        }
        .imgDiv img{
            width: 90%;
            margin: auto;
            max-width: 420px;
        }
    }
}

/* ============= info styling ======= */
.info{
    background-color: var(--SecondaryColor);
    .infoContainer{
        .titleDiv{
            flex-direction: column;
            text-align: center;
            gap: 1.5rem;
            justify-content: space-between;
        }
        .cardsDiv{
            gap: 1.5rem;
            margin-top: 3rem;

            .singleCard{
                background-color: var(--whiteColor);
                padding: 3rem 2rem;
                border-radius: 10rem;
                gap: 1rem;
                text-align: center;
                margin: auto;
                height: 330px;
                width: 80%;
                max-width: 250px;

                .iconDiv{
                    justify-content: center;
                    margin: auto;
                    background-color: var(--primarycolor);
                    max-width: fit-content;
                    height: 55px;
                    padding: 1rem;
                    border-radius: 3rem;
                    color: var(--whiteColor);
                }
                .cardTitle{
                    color: var(--blackColor);
                    font-weight: 700;                
                }
                .colorOne{
                    background-color: var(--colorOne);
                }
                .colorTwo{
                    background-color: var(--colorTwo);
                }
            }
        }

    }
    
}

/* ============= Lounge styling ======= */

.lounge{
    padding-bottom: 50px;
    .sectionContainer{
        width: 90%;
        margin: auto;
        gap: 1.5rem;

        .imgDiv{
            margin: auto;
        }
        .imgDiv img{
            width: 100%;
            max-width: 430px;
        }
        .textDiv{
            .grids{
                margin-top: 1rem;
                gap: 1.5rem;

                .singleGrid{
                    .gridTitle{
                        display: block;
                        padding-bottom: .5rem;
                        font-weight: 500;
                        color: var(--blackColor);
                    }
                }
            }
        }
    }
}

/* ============= traveler styling ======= */

.travelers{
    .sectionContainer{
        text-align: center;
        .travelersContainer{
            margin-top: 1rem;
            gap: 1.5rem;

            .singleTraveler{
                position: relative;
                height: 300px;
                width: 85%;
                overflow: hidden;
                border-radius: 10rem;
                max-width: 240px;
                gap: 1rem;
                margin: auto;
                cursor: pointer;

                .destinationImage , .travelerImage, .travelerName{
                    position: absolute;
                }

                .destinationImage{
                    height: 100%;
                    width: 100%;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    object-fit: cover;

                }
                .travelerImage{
                    height: 60px;
                    width: 60px;
                    object-fit: cover;
                    border-radius: 50%;
                    bottom: 45px;
                    left: 40%;
                    transform: translateY(-50%);
                    border:  5px solid rgba(248, 248, 248, 0.637);
                    transition: var(--transition);
                    animation: animated 3s infinite;
                }
                .travelerName{
                    bottom:-100px;
                    width: 100%;
                    text-align: center;
                    height: max-content;
                    background-color: var(--whiteColor);padding: 1.5rem 1rem;
                    transition: var(--transition);
                    animation: animatedBorder 1.6s infinite;

                   
                }
                &:hover{
                    .travelerName{
                        bottom: 0px;
                        transition: 1s ease-in-out;
                        
                    }
                    .destinationImage{
                        transition: 1s ease-in-out;
                        filter: blur(1.1px);
                    }
                    .travelerImage{
                        bottom: 104px;
                        transition: 1s ease-in-out;
                    }
                }
                .travelerName span{
                    display: block;
                    color: black;
                    font-weight: 700;
                }
                .travelerName p{
                    color: var(--primarycolor);
                    font-style: italic;
                    opacity: 0.9;
                }
            }
            .singleTraveler img{
                width: 100%;
            }
        }
    }
}
@keyframes animated {

    0%{transform: translateY(0px);}
    50%{transform: translateY(10px)}
    100%{transform: translateY(0px)}
    
}
@keyframes animatedBorder {
    0%{border-radius: 0rem;}
    50%{border-radius: 2rem;}
    100%{border-radius: 0rem;}
}


/* ============= subscribe styling ======= */

.subscribe{
    background-color: var(--SecondaryColor);
    margin-top: 50px;

    .sectionContainer{
        text-align: center;

        .inputDiv{
            margin-top: 1rem;
            flex-direction: column;
            background-color: var(--whiteColor);
            border-radius: 1rem;
        }
        .inputDiv input{
            border: none;
            outline: none;
            width:70%;
            margin: auto;
            background-color: transparent;
            padding: 1rem 1.5rem;

            &::placeholder{
                font-size: 15px;
                opacity: .8;

            }
        }
        .btn{
            width: 100%;
        }
    }
}


/* ============= footer styling ======= */
.footer{
    background-color: var(--SecondaryColor);

    .sectionContainer{
        gap: 2.5rem;

        .gridOne{
            gap: 1rem;
            justify-content: center;
            margin: auto;

            .logoDiv{
                .logo{
                    width: 56%;
                }
            }

            .socialIcon{
                margin-top:1rem ;
                gap: 1rem;

                .icon{
                    height: 40px;
                    width: 20px;
                    border-radius: 10rem;
                    border: 1px solid var(--borderColor);
                    padding: .4rem;
                    box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px,rgba(0,0,0,0.05) 0px 4px 6px -2px ;
                    color: rgb(114, 110, 110);
                    margin: 0 7px ;

                    &:hover{
                        background-color: var(--primarycolor);
                        transform: translateY(-7px);
                        transition: var(--transition);
                        color: white;
                    }
                }
            }
        }
        .footerLinks{
            display: grid;

            .linkTitle{
                color: var(--blackColor);
                font-weight: 700;
                display: block;
                font-size: 18px;
                padding-bottom: 1rem;

            }
        }
        .footerLinks li a{
            display: block;
            color: var(--greyText);
            transition: var(--transition);
            padding: .3rem 0;

            &:hover{
                transform:  translateX(7px);
                color: var(--primarycolor);
            }
        }
    }
    .copyRightDiv{
        justify-content: center;
        margin-top: 1.5rem;
        padding: 1rem;
        gap: 1rem;
        border-top: 2px solid var(--greyText);
    }
}


/* =============  media creean styling  ============= */


@media screen and (min-width: 480px) {
