    }
    /* ===== search styling ===-= */
    .search{
        .sectionContainer{
            .searchInputs{
                display: grid;
                grid-template-columns: repeat(2,1fr);
                .btn{
                    display: block;
                }
            }
        }
    }
    /* ===== travel styling ===-= */

    .travelers{
        .sectionContainer{
            .travelersContainer{
                grid-template-columns: repeat(2,1fr);
            }
        }
    }
    /* ===== subscribe styling ===-= */

    .subscribe{
        .sectionContainer{
            max-width: 80%;
            margin: auto;

            .inputDiv{
                flex-direction: row;
                padding: 0 .5rem;
                border-radius: 3rem;

                .btn{
                    max-width: 160px;
                }
            }
        }
    }
    /* ===== footer styling ===-= */
    .footer{
        .sectionContainer{
            grid-template-columns: repeat(2, 1fr);
            align-items: center;
            .footerLinks{
                justify-content: center;
            }
        }
    }
}

